.app--container {
    position: absolute;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    background: #404040;
    display: flex;
    flex-direction: column;
}

.app--navigation {
    padding: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.app--body.landing {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.app--body.quote {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.headers {
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 700px;
    width: 100%;
    margin-top: 50px;
}

.app--body.quote .headers {
    text-align: center;
    align-items: center;
    margin-top: 0 !important;
}

.app--body.landing .headers span:first-child {
    font-family: Brutal Type;
    color: #F3AD3D;
    font-size: 24px;
}

.app--body.landing .headers span:nth-child(2) {
    font-family: HurmeGeometricSans3W03-Lt;
    color: white;
    font-size: 65px;
}

.app--body.quote .headers span:nth-child(1) {
    font-family: HurmeGeometricSans3W03-Lt;
    color: white;
    font-size: 36px;
}

.app--body.landing .headers span:nth-child(3) {
    font-family: Brutal Type;
    color: #8D8D8D;
    font-size: 20px;
}

.app--body.quote .headers span:nth-child(2) {
    font-family: Brutal Type;
    color: #8D8D8D;
    font-size: 18px;
    max-width: 450px;
    width: 100%;
    margin-top: 30px;
}

.app--body.landing .headers span:nth-child(3) a {
    text-decoration: none;
    cursor: pointer;
    color: #F3AD3D;
    font-weight: 700;
}

.app--body.landing .social--media {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.social--media span {
    font-family: Brutal Type;
    color: #8D8D8D;
    font-weight: bold;
    margin-top: 20px;
    font-size: 24px;
}

.app--quote {
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    max-width: 600px;
    width: 90%;
}

.app--quote input, .app--quote textarea {
    margin-bottom: 18px;
    height: 55px;
    background: #585858;
    border: none;
    padding-left: 15px;
    padding-right: 15px;
    color: white;
    font-family: Brutal Type;
    font-size: 16px;
    outline: none;
    border-radius: 0px !important;
    -webkit-appearance: none;
}

.app--quote textarea {
    padding-top: 15px;
    padding-bottom: 15px;
    height: 250px;
    resize: none;
}

.app--quote input::placeholder, .app--quote textarea::placeholder {
    color: #8D8D8D;
}

.app--quote .submit {
    flex: 1;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.app--quote input[type=submit] {
    width: 250px;
    background: #F3AD3D;
    color: white;
    cursor: pointer;
    border-radius: 0px !important;
}

.hidden {
    display: none !important;
}

