html, body, .root, #root {
    margin: 0;
    padding: 0;
}

.app--container {
    overflow: auto;
}

@media only screen and (max-width: 800px) {
    .app--navigation a:nth-child(1) svg {
        width: 90px;
    }

    .app--navigation a:nth-child(2) svg {
        width: 25px !important;
    }

    .app--body.landing svg:first-of-type, .main--logo {
        width: 160px !important;
        height: auto;
    }

    .app--body.landing .headers {
        max-width: 300px !important;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .landing .headers span:nth-child(1) {
        font-size: 16px !important;
    }

    .landing .headers span:nth-child(2), .quote .headers span:nth-child(1) {
        text-align: center;
        font-size: 40px !important;
        margin-top: 10px;
    }

    .landing .headers span:nth-child(3), .quote .headers span:nth-child(2) {
        text-align: center;
        font-size: 14px !important;
        margin-top: 20px !important;
        max-width: 300px !important;
    }
    
    .quote input[type=submit] {
        flex: 1 !important;
    }

    .app--navigation {
        padding: 30px !important;
    }

    .quote .app--quote textarea {
        height: 300px !important;
    }
}
